*,*::before,*::after{margin:0 auto;padding:0;box-sizing:inherit}a{text-decoration:none}ul,li,ol{margin:0;padding:0;list-style:none}h1,h2,h3,h4,h5,h6,p{margin:0;padding:0}​img{display:block;max-width:100%;height:auto}button:hover{cursor:pointer}button:focus{outline:none}input:focus,input:active,button:active,button:focus{outline:none}html{box-sizing:border-box;font-family:"Roboto",sans-serif;font-weight:normal;font-size:16px;scroll-behavior:smooth}body{background-color:#000}